import React from "react";
import { cnb } from "cnbuilder";

// import specific templates for the component
import { Container } from "../../packages/grids/Container";

/**
 * Base component
 *
 * @param {string} title The component title
 * ... any other options needed
 * @returns {JSX.Element}
 * @constructor
 */

export default function ImageQuote({ displayConfiguration, data }) {
  const { imageCaption, imageCredit, quote, name, title } =
    displayConfiguration;

  const imageOrientation =
    data.attributes.height > data.attributes.width ? "portrait" : "landscape";

  const imageWidthMaps = new Map();
  imageWidthMaps.set("landscape", {
    image: "su-w-5/6 md:su-w-[75%] lg:su-w-[calc(50%-1.9rem)]",
    quote: "lg:su-w-[62%] lg:su-ml-[-10%]",
    line: "before:su-w-[18%] md:before:su-w-[26%] lg:before:su-w-[52%] dark:before:su-rotate-180",
  });
  imageWidthMaps.set("portrait", {
    image: "su-w-5/6 md:su-w-[58.33%] lg:su-mr-[2%] lg:su-w-[38%]",
    quote: "lg:su-w-[70%] lg:su-ml-[-10%]",
    line: "before:su-w-[18%] md:before:su-w-[42%] lg:before:su-w-[62%] dark:before:su-rotate-180",
  });

  const widthClasses = imageWidthMaps.get(imageOrientation);
  const captionCredit =
    imageCaption && imageCredit
      ? `${imageCaption} | ${imageCredit}`
      : imageCaption || imageCredit;

  return (
    <Container width="large">
      <section
        className={cnb(
          "story__quote su-relative",
          "su-flex su-flex-wrap lg:su-flex-nowrap lg:su-items-start",
          widthClasses.line,
          "before:su-absolute before:su-h-4 before:su-z-0 before:su-right-0 before:su-top-45 lg:before:su-top-72 su-bg-gradient-before-reverse",
          "after:su-absolute after:su-w-4 after:su-z-[-10] after:su-h-[calc(100%-4.5rem)] after:su-right-0 after:su-top-45 lg:after:su-top-72 su-bg-gradient-after-reverse",
          imageOrientation === "landscape"
            ? "md:before:su-top-[20%] md:after:su-top-[20%] md:after:su-h-[80%] lg:after:su-h-[calc(100%-16rem)]"
            : "md:before:su-top-[30%] md:after:su-top-[30%] md:after:su-h-[70%] lg:after:su-hidden"
        )}
      >
        <figure
          className={cnb("su-flex su-flex-col su-gap-8", widthClasses.image)}
        >
          <img
            src={data.url}
            className="su-z-10 su-w-full su-h-auto su-object-center su-object-cover"
            alt={data.attributes.alt}
          />
          <figcaption className="dark:su-text-white su-text-14 su-font-normal su-mt-8 md:su-text-16">
            {captionCredit}
          </figcaption>
        </figure>

        <figure
          className={cnb(
            "su-ml-0 md:su-w-[83.32%]]",
            "su-pl-32 su-pr-45 md:su-pr-0 md:su-pl-50 md:su-mx-[8.33%] lg:su-mx-0 lg:su-pl-[17.7%] lg:su-mt-72 su-flex su-flex-col su-pt-49 lg:su-pt-90 su-gap-20 md:su-gap-26 lg:su-gap-27 lg:su-pr-72 su-relative",
            widthClasses.quote,
            imageOrientation === "portrait" &&
              "after:su-hidden lg:after:su-block su-bg-gradient-after-reverse after:su-absolute after:su-w-4 after:su-z-[-10] after:su-h-[calc(100%-8.8rem)] after:su-right-0 after:su-top-0"
          )}
        >
          <blockquote
            className={cnb(
              "story__quote-target su-relative su-pl-0 su-flex su-m-0 su-items-start su-gap-6",
              "su-font-serif su-font-semibold su-text-black dark:su-text-white su-text-24 md:su-text-[3.6rem] su-leading-[1.5] lg:su-leading-[1.32]",
              "before:su-content-['“'] before:su-absolute before:su-right-full before:su--mt-30 md:before:su--mt-25 lg:before:su--mt-32 before:su-mr-6 md:before:su-mr-13 ",
              "before:su-font-serif before:su-text-black dark:before:su-text-white before:su-text-[5.9rem] md:before:su-text-[7.3rem] lg:before:su-text-[9.3rem] before:su-leading-[1.85] md:before:su-leading-[1.6] lg:before:su-leading-[1.4]"
            )}
          >
            {`${quote}”`}
          </blockquote>

          <figcaption className="su-relative su-text-black su-basefont-21 su-mt-0 su-flex su-flex-col dark:su-text-white su-text-16 md:su-text-21 su-font-normal lg:su-flex su-gap-5 md:su-gap-6">
            <strong>{name}</strong> <span>{title}</span>
          </figcaption>
        </figure>
      </section>
    </Container>
  );
}
